@import url('https://fonts.googleapis.com/css2?family=Honk&display=swap');

:root {
  --black: rgba(0, 0, 0, 1);
  --dark-grey: rgba(0, 0, 0, 0.60);
  --light-grey: rgb(222, 220, 220);
  --white: rgba(255, 255, 255, 1);
  --pink: rgb(255 70 175);
  --red:  rgb(251, 81, 81);
  --purple: rgb(158, 6, 241);
  --darkblue: rgb(14 43 106);
  --sand: rgb(228, 206, 163);
}
/** @@@ MOBILE - DEFAULT @@@ */
html {
  background-image: url('./img/PandaMylk-mobile-800.jpg');
  background-color: var(--sand);
  background-size: cover;
  background-repeat: no-repeat;
}

.App {
  text-align: center;
  color: var(--purple);
  font-family: "Honk", system-ui;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings:
    "MORF" 15,
    "SHLN" 50;
}

.App-header h1 {
  font-size: 4rem;
}

.album {
  display: flex;
  justify-content: center;
  align-items: center;
}

.album-cover {
  width: 200px;
  border-radius: 1rem;
  padding-top: 12rem;
}

/* @@@ LARGE MOBILE @@@ */
@media only screen and (min-width: 500px) {
  html {
    background-image: url('./img/PandaMylk-1600.jpg');
    background-size: initial;
  }
  .App-header h1 {
    font-size: 8rem;
  }
  .album-cover {
    width: 300px;
    border-radius: 1rem;
    padding-top: 0;
  }  
}

/* @@@ DESKTOP @@@ */
@media only screen and (min-width: 1200px) {
  html {
    background-image: url('./img/PandaMylk-1600.jpg');
    background-size: cover;
  }
  .App-header h1 {
    font-size: 10rem;
  }  
}

/* Fancy Anim */
.glow {
  animation-duration: 2s;
  animation-name: glow;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
}
@keyframes glow {
  from {
    text-shadow: -2px 2px var(--pink);
  } to {
    text-shadow: 2px 2px var(--black);
  }
}
